.menu-container {
  width: 100vw;
  height: 100vh;
  z-index: 999;
  background-color: #fcfcfc;
  padding: 30px 20px;
  transition: transform .25s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.menu-container__cap {
  align-items: center;
  display: flex;
}

.menu-container__input {
  height: 0;
  width: 0;
  visibility: hidden;
}

.menu-container__label {
  cursor: pointer;
  text-indent: -9999px;
  width: 40px;
  height: 20px;
  background: #fcfcfc;
  border: 1px solid #fd5103;
  border-radius: 100px;
  display: block;
  position: relative;
}

.menu-container__label:after {
  content: "";
  width: 18px;
  height: 18px;
  background: #fd5103;
  border-radius: 50%;
  transition: all .3s;
  position: absolute;
  top: 0;
  left: 5px;
}

input:checked + label {
  background: #fd5103;
}

input:checked + label:after {
  background: #fcfcfc;
  left: calc(100% - 5px);
  transform: translateX(-100%);
}

label:active:after {
  width: 15px;
}

.menu-container.is-open {
  transform: translateX(0);
}

